import * as React from 'react';
import { graphql, useStaticQuery } from 'gatsby';

import classNames from 'classnames/bind';
import * as styles from './imprintDataProtection.module.scss';

import SEO from '../components/seo';

import Markdown from 'react-markdown';

const cx = classNames.bind(styles);

const ImprintDataProtection: React.FC = () => {
  const { mpapi } =
    useStaticQuery<GatsbyTypes.ImprintDataProtectionQuery>(graphql`
      query ImprintDataProtection {
        mpapi {
          imprintDataProtectionPage {
            data {
              attributes {
                imprint
                dataProtection
              }
            }
          }
        }
      }
    `);

  if (!mpapi) return null;
  const imprintDataProtectionPage =
    mpapi.imprintDataProtectionPage.data.attributes;

  return (
    <>
      <SEO
        title={'Data Protection | Atelier Mendgen Peissl'}
        description="Data protection page for Atelier Mendgen Peissl"
        image="https://mendgenpeissl.com/logo.jpg"
      />
      <div className={cx('imprintDataProtection')}>
        <h1>Impressum und Datenschutzerklärung</h1>

        <h2 className={cx('title')}>Impressum</h2>
        {imprintDataProtectionPage.imprint && (
          <Markdown className={cx('content')}>
            {imprintDataProtectionPage.imprint}
          </Markdown>
        )}

        <h2 className={cx('title')}>Datenschutzerklärung</h2>
        {imprintDataProtectionPage.dataProtection && (
          <div
            className={cx('content')}
            dangerouslySetInnerHTML={{
              __html: imprintDataProtectionPage.dataProtection,
            }}
          />
        )}
      </div>
    </>
  );
};

export default ImprintDataProtection;
